html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #8ec9e6;
  color: #FFFFFF;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Press Start 2P", cursive;
  font-size: 1.6rem;
}

.contenedor {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
  color: #000000;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Press Start 2P", cursive;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

td {
  color: #000000;
}

/** Media Queries **/
.seccion {
  margin-top: 2.5rem 0;
  margin-bottom: 2.5rem 0;
}

.contenido-centrado {
  max-width: 80rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.boton {
  margin: 1rem 0 1rem 0;
}

.boton-amarillo {
  background-color: #e64e3d;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem 0;
  display: inline-block;
  border: none;
}
.boton-amarillo:hover {
  background-color: #e33926;
  cursor: pointer;
}

.boton-amarillo-block {
  background-color: #e64e3d;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem 0;
  display: block;
  border: none;
}
.boton-amarillo-block:hover {
  background-color: #e33926;
  cursor: pointer;
}

.boton-azul {
  background-color: #8ec9e6;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem 0;
  display: inline-block;
  border: none;
}
.boton-azul:hover {
  background-color: #79bfe1;
  cursor: pointer;
}

.boton-rojo-block {
  background-color: #e64e3d;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem 0;
  display: block;
  border: none;
  width: 100%;
}
.boton-rojo-block:hover {
  background-color: #e33926;
  cursor: pointer;
}

.boton-azul-block {
  background-color: #8ec9e6;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem 0;
  display: block;
  border: none;
}
.boton-azul-block:hover {
  background-color: #79bfe1;
  cursor: pointer;
}

.parte-superior {
  max-width: 90rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.contenedor-pokemon_day {
  background-color: #64b5dd;
  max-width: 80rem;
  margin: 2rem auto;
  box-shadow: 0px 4px 8px #000000;
  padding-bottom: 1rem;
  border-radius: 1rem;
}
.contenedor-pokemon_day h4 {
  margin: 0;
  margin-bottom: 1rem;
  margin-left: 2rem;
  font-size: 2.4rem;
}

#name {
  text-align: left;
  text-transform: uppercase;
  font-weight: 700;
  margin-left: 2rem;
}

.contenedor-info {
  display: grid;
  grid-template-columns: 60% 40%;
}
.contenedor-info p {
  margin: 1rem 2rem;
}

.imagen {
  display: flex;
  justify-content: center;
}
.imagen img {
  margin-top: 2rem;
}

.type-pokemon {
  margin: 0;
  margin-left: 2rem;
  text-transform: uppercase;
}

.contenedor {
  max-width: 80rem;
}

.contenedor-acciones {
  display: flex;
  justify-content: flex-end;
  min-width: 100%;
}

.header {
  display: flex;
  align-items: center;
  background-color: #e64e3d;
}
@media (min-width: 768px) {
  .header {
    height: 8rem;
  }
}
.header p {
  margin-right: 2rem;
}

.busqueda-cont {
  margin-right: auto;
  display: flex;
  justify-content: center;
  height: 4rem;
}
.busqueda-cont input, .busqueda-cont button {
  min-width: 4rem;
  height: 4rem;
}
.busqueda-cont input {
  margin-left: 1rem;
  width: 100%;
  display: none;
}
.busqueda-cont button {
  max-width: 4rem;
}
.busqueda-cont .busqueda-submit {
  display: block;
  margin-left: 1rem;
}
.busqueda-cont .busqueda-submit:hover {
  cursor: pointer;
}
@media (min-width: 1024px) {
  .busqueda-cont .form-busqueda {
    display: flex;
    margin-left: 2rem;
  }
  .busqueda-cont .busqueda-submit {
    display: flex;
    margin-left: 0;
  }
  .busqueda-cont input {
    display: block;
  }
}

.pokedex {
  margin: 2 auto;
  display: flex;
  justify-content: center;
  align-items: center;
}
.pokedex a {
  max-width: 100%;
  padding: 1rem 0 0 0;
  font-size: 2.6rem;
}
@media (min-width: 480px) {
  .pokedex a {
    font-size: 4rem;
  }
}

.navegador {
  margin-left: auto;
  display: flex;
}
.navegador a {
  transition: background-color 0.5s ease-in;
  padding: 1rem;
  border-radius: 0.5rem;
  margin: 0 auto;
}
.navegador a:hover {
  background-color: #e64e3d;
}

.enlaces-nav {
  transition: opacity 0.3s ease-in-out;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  background-color: #e64e3d;
  box-shadow: 0 1rem 10rem #000000;
  width: 20rem;
  height: 100vh;
  position: absolute;
  top: 0;
  right: 0;
  transition: all 0.5s ease;
  z-index: 10;
}
.enlaces-nav a:hover {
  transform: scale(1.1);
  background-color: #d52e1b;
}
@media (min-width: 1024px) {
  .enlaces-nav {
    background-color: transparent;
    box-shadow: none;
    display: flex;
    flex-direction: row;
    position: static;
    height: 5rem;
    width: 100%;
    align-items: center;
  }
  .enlaces-nav a:hover {
    transform: scale(0.9);
    background-color: #d52e1b;
    filter: saturate(100%);
  }
}

.item-nav {
  margin-right: 2rem;
}
.item-nav svg:hover {
  cursor: pointer;
  background-color: #de301d;
}
@media (min-width: 1024px) {
  .item-nav {
    display: none;
  }
}

.oculta {
  display: none;
}

.cerrar {
  text-align: center;
  padding: 1rem;
  background-color: #f2a097;
  margin-left: auto;
  border-radius: 0.5rem;
}
.cerrar:hover {
  cursor: pointer;
}
@media (min-width: 1024px) {
  .cerrar {
    display: none;
  }
}

.overlay, .fondo-cierra {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 9999;
}

.ventana-emergente {
  display: block;
  position: absolute;
  text-align: center;
  border-radius: 1rem;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -150%);
  background-color: #fff;
  width: 100%;
  max-width: 400px;
  height: 200px;
  z-index: 10000;
}
.ventana-emergente .cerrar {
  max-width: 4rem;
  margin: 1rem;
  float: right;
}
.ventana-emergente input {
  z-index: 10001;
  width: 90%;
  height: 4rem;
  margin-bottom: 1rem;
}
.ventana-emergente button {
  width: 85%;
  margin: 0 auto;
}

.footer {
  margin-top: auto;
  background-color: #e1e1e1;
  width: 100%;
  max-height: 7rem;
  margin-top: 3rem;
}
.footer h3 {
  font-size: 2rem;
  margin: 1rem auto;
  max-width: 50rem;
  color: #000000;
}

.ventana-error {
  background-color: #e64e3d;
  text-align: center;
  padding: 1rem;
  border-radius: 1rem;
}
.ventana-error h2, .ventana-error p {
  margin: 1rem;
}

.contenedor-mensaje {
  background-color: #e1e1e1;
  padding: 1rem;
  min-height: 20rem;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 10rem;
  margin-bottom: 10rem;
  padding-bottom: 10 rem;
  color: #000000;
  border-radius: 2rem;
  box-shadow: 0 2rem 1rem;
}
.contenedor-mensaje h2 {
  font-size: 3rem;
}

.Mis h4 {
  margin: 1rem auto;
  text-align: center;
  color: #4b4e6c;
}

.contenedor-favorito {
  display: grid;
  grid-template-columns: 40% 60%;
  margin-bottom: 2rem;
}
.contenedor-favorito p {
  font-size: 1.2rem;
}
@media (min-width: 768px) {
  .contenedor-favorito p {
    font-size: 1.8rem;
  }
}

.my-favorite {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #e64e3d;
  width: 80%;
  margin-left: auto;
  border-radius: 1rem 0 0 1rem;
  box-shadow: 0.4rem 1rem 1rem #000000;
  z-index: 100;
}
.my-favorite p {
  text-transform: capitalize;
  margin-bottom: 0;
}

.habilidades {
  background-color: #4fabd8;
  width: 80%;
  margin-right: auto;
  padding-left: 1rem;
  border-radius: 0 1rem 1rem 0;
  box-shadow: 0 1rem 1rem #000000;
}
.habilidades p:first-child {
  text-align: center;
}
.habilidades p {
  margin-left: 1rem;
  text-transform: capitalize;
}

.contenedor-grupos-pokedex {
  background-color: #e64e3d;
  border-radius: 1rem;
}
.contenedor-grupos-pokedex h3 {
  margin: 0;
  padding-top: 0.3rem;
}

.only-pokemones {
  margin: 1rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  box-shadow: 0 1rem 1rem #000000;
  background-color: #64b5dd;
  border-radius: 1rem;
}
@media (min-width: 480px) {
  .only-pokemones {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 768px) {
  .only-pokemones {
    grid-template-columns: repeat(4, 1fr);
  }
}
@media (min-width: 1024px) {
  .only-pokemones {
    grid-template-columns: repeat(6, 1fr);
  }
}

.pokemon-equipo {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #e1e1e1;
  margin: 1rem;
  border-radius: 1rem;
}
.pokemon-equipo .nombre-pokemon-equipo {
  text-align: center;
  margin-top: 1rem;
  margin-bottom: 0;
}

.without-pokemon {
  width: 70%;
  height: 70%;
  margin-top: 2rem;
}
.without-pokemon svg {
  height: 100%;
  width: 100%;
}

.acciones-pokedex {
  display: flex;
  width: 100%;
  justify-content: center;
}
.acciones-pokedex form {
  margin: 0;
  width: 100%;
}
.acciones-pokedex button {
  margin: 0;
  height: 100%;
  width: 100%;
  text-align: center;
  font-size: 0.8rem;
  padding-bottom: 0;
  background-color: #e64e3d;
  border-radius: 0.5rem;
  outline: none;
  border: 0.5px solid #000000;
}
.acciones-pokedex button:hover {
  cursor: pointer;
}

.contenedor-acciones-pokedex {
  margin: 0;
  padding: 0.5rem;
  padding-top: 0;
}

.no-pokemon {
  height: 7rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.no-pokemon p {
  font-size: 2.4rem;
}

.titulo-equipo {
  display: flex;
  justify-content: center;
  gap: 1rem;
}
.titulo-equipo .nombre-equipo {
  margin-top: 1rem;
}

.boton-editar-nombre {
  background-color: transparent;
  border: 0;
  margin-top: 1rem;
}
.boton-editar-nombre:hover {
  cursor: pointer;
}

@media (min-width: 1024px) {
  .cambio-nombre {
    display: flex;
    justify-content: center;
    align-items: flex-start;
  }
  .cambio-nombre form {
    margin-top: 4rem;
  }
}

.contenedor-lista {
  width: 100%;
  max-width: 120rem;
  background-color: #e1e1e1;
  border-radius: 1rem 1rem 0 0;
  box-shadow: 0 1rem 1rem #000000;
  z-index: 1000;
}

.filtros-busqueda {
  background-color: #4b4e6c;
  padding: 1rem;
  border-radius: 1rem 1rem 0 0;
  box-shadow: 0 1rem 1rem #000000;
  z-index: 900000;
}
@media (min-width: 768px) {
  .filtros-busqueda {
    display: flex;
    justify-content: center;
  }
}
.filtros-busqueda div {
  display: flex;
  flex-direction: column;
  margin: 0 1rem;
}
.filtros-busqueda form {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 1rem;
}
.filtros-busqueda form select {
  height: 1.8rem;
  font-size: 1rem;
  margin-bottom: 1rem;
  border-radius: 0.7rem;
  background-color: #8ec9e6;
}
.filtros-busqueda form label {
  margin-bottom: 0.5rem;
}
.filtros-busqueda form option {
  background-color: #8ec9e6;
}
@media (min-width: 480px) {
  .filtros-busqueda form select {
    height: 2rem;
    font-size: 1.6rem;
  }
}
@media (min-width: 768px) {
  .filtros-busqueda form {
    display: flex;
    justify-content: center;
    grid-gap: 0;
  }
  .filtros-busqueda form select {
    height: 2.4rem;
    font-size: 2rem;
  }
}
.filtros-busqueda button {
  max-width: 4rem;
  max-height: 4rem;
  padding: 0;
  background-color: #8ec9e6;
  border: #e1e1e1;
  margin-left: 1rem;
}
.filtros-busqueda button svg {
  max-height: 100%;
  max-width: 100%;
  transform: translateY(-5%);
}
.filtros-busqueda button:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .filtros-busqueda button {
    margin-top: 1rem;
  }
}

.pokemons-busqueda-lista {
  max-width: 100%;
  margin: 0 auto;
  padding: 1rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
}
.pokemons-busqueda-lista div {
  padding: 0 0.5rem;
}
.pokemons-busqueda-lista p {
  color: #4b4e6c;
}
@media (min-width: 768px) {
  .pokemons-busqueda-lista {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 1024px) {
  .pokemons-busqueda-lista {
    grid-template-columns: repeat(5, 1fr);
  }
}

.pokemon-lista {
  box-shadow: 0 3px 5px #000000;
  background-color: #68b7de;
  border-radius: 1rem;
  width: 100%;
}
.pokemon-lista div:nth-child(2) {
  display: flex;
  justify-content: center;
}
.imagen-pokemon-lista {
  max-height: 100%;
}
.imagen-pokemon-lista:hover {
  transform: scale(1.2);
}

.identificador {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.identificador p {
  text-transform: capitalize;
  margin: 0.5rem 0;
  font-size: 1rem;
}
@media (min-width: 480px) {
  .identificador p {
    font-size: 1.3rem;
  }
}
.identificador p:last-child {
  padding: 0.5rem;
  border-radius: 3px;
  background-color: #4b4e6c;
  color: #FFFFFF;
}

.botones-pokemon-lista {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding: 0;
  height: 5rem;
}

.boton-lista {
  max-height: 100%;
  padding: 0;
}
.boton-lista .form-acciones {
  padding: 0;
}
.boton-lista button {
  height: 100%;
  padding: 0;
  margin: 0;
}
.boton-lista button svg {
  height: 100%;
}
.boton-lista .boton-accion {
  height: 100%;
  max-width: 100%;
}

.busqueda input, .busqueda button {
  margin: 1rem 0;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
  color: #333;
  background-color: #f5f5f5;
}
.busqueda input:focus {
  outline: none;
  border-color: #6c63ff;
  box-shadow: 0 0 5px #6c63ff;
}
.busqueda button:hover {
  cursor: pointer;
}

.form-styles input {
  background-color: #FFFFFF;
  color: #4b4e6c;
}
.form-styles input:focus {
  outline: none;
  border-color: #6c63ff;
  box-shadow: 0 0 5px #6c63ff;
}

.boton-styles {
  background-color: #e33926;
}

.estadisticas {
  padding-top: 2rem;
  margin: 0 auto;
  color: #000000;
  max-width: 80rem;
  padding: 3rem 3rem 5rem;
  background-color: #e1e1e1;
  box-shadow: 0 8px 9px #000000;
  border-radius: 0.5rem;
}
.estadisticas h2 {
  text-align: center;
  margin: 1rem 0;
}

.stats:not(:last-child) {
  margin-bottom: 3rem;
}

.detalles {
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-bottom: 1rem;
}

.barra {
  position: relative;
  width: 100%;
  height: 20px;
  border: 1px;
  border: 1px solid #8ec9e6;
  border-radius: 2rem;
}
.barra div {
  position: relative;
  width: 0;
  height: 8px;
  border-radius: 1rem;
  background-color: #8ec9e6;
}

#hp-barra {
  height: 100%;
  width: 0;
  background-color: #8ec9e6;
}

#barra {
  height: 100%;
  width: 0%;
  background-color: #8ec9e6;
}

.boton-accion {
  max-width: 90%;
  height: 80%;
  border-radius: 1rem;
  margin: 1rem;
  background-color: #8ec9e6;
  box-shadow: 0px 1px 4px #000000;
}
.boton-accion svg {
  padding: -1rem;
  width: 3.7rem;
}
.boton-accion:hover {
  cursor: pointer;
  transform: scale(1.1);
}

.fondo {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.2);
  z-index: 10000;
}

.contenedor-add {
  display: flex;
  justify-content: center;
  align-items: center;
}

.deten {
  display: block;
  position: absolute;
  text-align: center;
  border-radius: 1rem;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -150%);
  background-color: #fff;
  max-width: 400px;
  width: 80%;
}

.add {
  margin-top: 0em;
}

.form-acciones {
  height: 100%;
}

.grupo-usuario {
  width: 100%;
}
.grupo-usuario:last-child {
  margin-bottom: 1rem;
}
.grupo-usuario button {
  width: 75%;
  height: 80%;
  border-radius: 1rem;
  margin-top: 1rem;
  background-color: #f0b7c2;
  box-shadow: 0px 1px 4px #000000;
}
.grupo-usuario button:hover {
  cursor: pointer;
  transform: scale(0.9);
}

.boton-cerrar {
  margin-top: 1rem;
  background-color: #e64e3d;
  height: 3rem;
  width: 3rem;
  border-radius: 0.5rem;
}
.boton-cerrar:hover {
  cursor: pointer;
  transform: scale(0.9);
}

.alerta {
  margin: 1rem;
  padding: 0.5rem;
  border-radius: 1rem;
  box-shadow: 0 0.5rem 1rem;
}
.alerta p {
  margin: 1rem;
  text-align: center;
  text-transform: uppercase;
}

.advertencia {
  background-color: #8ec9e6;
}

.exito {
  background-color: #f0b7c2;
}

.error {
  background-color: #e64e3d;
}

.contenedor-formulario {
  margin-top: 10rem;
  max-height: 100%;
  padding: 1rem;
  min-height: 34rem;
  margin: auto 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.formulario {
  width: 100%;
  background-color: #FFFFFF;
  padding: 2rem;
  color: #000000;
  border-radius: 2rem;
  box-shadow: 0 1rem 1rem;
  max-width: 60rem;
}
.formulario .description-form {
  text-align: center;
}

.entrada-formulario {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem;
  margin: 1rem;
  background-color: #e64e3d;
  display: grid;
  grid-template-columns: 30% 70%;
  border-radius: 1rem;
}
.entrada-formulario input {
  border-radius: 0.5rem;
  height: 3rem;
  font-size: 1rem;
}
.entrada-formulario label {
  font-size: 1rem;
}
@media (min-width: 480px) {
  .entrada-formulario label {
    font-size: 1.3rem;
  }
  .entrada-formulario input {
    font-size: 1.4rem;
  }
}
@media (min-width: 768px) {
  .entrada-formulario label {
    font-size: 1.6rem;
    margin-right: 0.5rem;
  }
  .entrada-formulario input {
    font-size: 1.7rem;
  }
}

.boton-send {
  background-color: #ef8c81;
  padding: 0.5rem;
  margin-left: 1rem;
  border-radius: 1rem;
}
.boton-send:hover {
  cursor: pointer;
}

.description-form {
  margin: 1rem;
}

.contenedor-log-in {
  max-width: 60rem;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.contenedor-log-in a {
  font-size: 1.4rem;
  margin: 0;
  border-radius: 1rem;
  box-shadow: 0 1rem 1rem;
  padding: 1rem;
  background-color: #f0b7c2;
}
.contenedor-log-in a:hover {
  cursor: pointer;
}

.botones-paginador {
  padding: 1rem;
  padding-bottom: 0;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.botones-paginador button {
  background-color: #4b4e6c;
  color: #FFFFFF;
  transition: ease-out;
}
.botones-paginador button:hover {
  cursor: pointer;
  transform: scale(1.1);
}
.botones-paginador:last-child {
  padding-bottom: 1rem;
}